.btn-primary { color: var(--purple); font-weight: 700; display: inline-flex; align-items: center; gap: 1rem; }
.btn-primary i.ph-arrow-right { width: 2rem; height: 2rem; border-radius: 50%; border: 1px solid var(--purple); flex-shrink: 0; display: flex; align-items: center; justify-content: center; transition: background-color 0.3s, transform 0.3s; }
.btn-primary:hover {  }
.btn-primary:hover span { text-decoration: underline; }
.btn-primary:hover i.ph-arrow-right { transform: translateX(0.25rem); color: #fff; background: var(--purple); }

.btn-secondary { color: var(--creme); font-weight: 700; display: inline-flex; align-items: center; gap: 1rem; }
.btn-secondary i.ph-arrow-right { width: 2rem; height: 2rem; border-radius: 50%; border: 1px solid var(--creme); flex-shrink: 0; display: flex; align-items: center; justify-content: center; transition: background-color 0.3s, transform 0.3s; }
.btn-secondary:hover {  }
.btn-secondary:hover span { text-decoration: underline; }
.btn-secondary:hover i.ph-arrow-right { transform: translateX(0.25rem); color: #000; background: var(--creme); }

.btn-tertiary { color: #fff; font-weight: 700; display: inline-flex; align-items: center; gap: 1rem; }
.btn-tertiary i.ph-arrow-right { width: 2rem; height: 2rem; border-radius: 50%; border: 1px solid #fff; flex-shrink: 0; display: flex; align-items: center; justify-content: center; transition: background-color 0.3s, transform 0.3s; }
.btn-tertiary:hover {  }
.btn-tertiary:hover span { text-decoration: underline; }
.btn-tertiary:hover i.ph-arrow-right { transform: translateX(0.25rem); color: #000; background: #fff; }

.btn-bordered { border: 1px solid var(--purple); padding: 0.75rem 1rem; border-radius: 10rem; }
.btn-bordered:hover { background-color: var(--purple); color: #fff; }


.center-title {  }
.text-center { text-align: center; }

.purple-wall { background: var(--purple); }

nav.navbar { position: fixed; width: 100vw; z-index: 10; color: #fff; pointer-events: none; mix-blend-mode: difference; }
nav.navbar .nav-wrap { display: flex; justify-content: space-between; gap: 1rem; padding: 1rem 0; align-items: center; }
nav.navbar.scroll-nav { }
nav button.menu-toggle { cursor: pointer; display: flex; align-items: center; gap: 1rem; border: none; padding: 0; background: none; color: #fff; outline: none; pointer-events: auto; }
nav button.menu-toggle .hamburger-wrap { width: 2.5rem; height: 2.5rem; position: relative; border-radius: 50%; border: 2px solid #fff; padding: 0; display: flex; flex-direction: column; justify-content: center; align-items: center; gap: 0.25rem; background: none; color: #fff; outline: none; transition: border-color 0.3s, background-color 0.3s; }
nav button.menu-toggle:hover .hamburger-wrap { border-color: var(--purple); background: rgb(255 255 255 / 25%); }
nav button.menu-toggle:active .hamburger-wrap,
nav button.menu-toggle:focus .hamburger-wrap { background: rgba(190, 53, 109, 0.8); color: #fff; border-color: var(--purple); box-shadow: 0 0 0 0.25rem rgba(190, 53, 109, 0.5); }
nav button.menu-toggle:active:focus .hamburger-wrap { background: var(--purple); }
nav button.menu-toggle .hamburger-line { border-top: 1px solid #fff; width: 50%; transition: border-color 0.3s, transform 0.3s, opacity 0.3s; position: absolute; }
nav button.menu-toggle .hamburger-line:nth-child(1) { transform: translateY(-0.25rem); }
nav button.menu-toggle .hamburger-line:nth-child(2) {  }
nav button.menu-toggle .hamburger-line:nth-child(3) { transform:translateY(0.25rem); }

nav button.menu-toggle.toggled .hamburger-line:nth-child(1) { transform: rotate(45deg); }
nav button.menu-toggle.toggled .hamburger-line:nth-child(2) { transform: rotate(180deg); opacity: 0; }
nav button.menu-toggle.toggled .hamburger-line:nth-child(3) { transform: rotate(-45deg); }

.contact-info { display: flex; justify-content: space-between; gap: 1rem; line-height: 1.6; }
.contact-info a { color: inherit; text-decoration: none; }
.contact-info a:hover { text-decoration: underline; }


