.agenda-items { margin-bottom: 5rem; }
.agenda-item { display: flex; gap: 2.5rem; border-bottom: 1px solid #626160; padding: 3rem 0; }
.agenda-item:last-child { margin-bottom: 5rem; }
.agenda-item .img-wrap { width: 25%; flex-shrink: 0; display: flex; align-items: center; }
.agenda-item .img-wrap img { width: 100%; aspect-ratio: 3/2; }
.agenda-item .img-wrap .no-img-grey { width: 100%; aspect-ratio: 3/2; background: #eee; }
.agenda-item .content-wrap { padding: 1.5rem 0; display: flex; flex-direction: column; }
.agenda-item .content-bottom { margin-top: auto; }
.agenda-item .small-desc { color: #626160; max-width: 30rem; }
.agenda-item .agenda-item-attributes { display: flex; align-items: center; gap: 3rem; }
.agenda-item .agenda-item-attributes .attribute { display: flex; align-items: center; gap: 0.25rem; font-weight: 300; }
.agenda-item .agenda-item-attributes i { color: var(--purple); font-size: 1.25rem; }
.agenda-item .link-wrap { display: flex; align-items: center; margin-left: auto; }
.agenda-item .link-wrap a { color: var(--purple); text-decoration: none; width: 3rem; height: 3rem; font-size: 1.75rem; border-radius: 50%; border: 2px solid var(--purple); display: flex; align-items: center; justify-content: center; transition: background-color 0.3s, color 0.3s; }
.agenda-item .link-wrap a:hover { background-color: var(--purple); color: #fff; }

.go-to-posts-wrap { display: flex; justify-content: center; }

.blog-items-list { margin-bottom: 3rem; }
.blog-item {  }
.blog-item .content-wrap { height: 100%; display: flex; flex-direction: column; }
.blog-item .content-bottom { margin-top: auto; }
.blog-item .img-wrap { margin-bottom: 1.125rem; display: block; overflow: hidden; }
.blog-item .img-wrap img { width: 100%; transition: transform 0.3s; }
.blog-item .img-wrap:hover img { transform: scale(1.05); }
.blog-item h3 { font-size: 1.5rem; font-weight: 500; margin-bottom: 0.5rem; }
.blog-item h4 { color: #AFAFAF; font-weight: 400; font-size: 1rem; margin-bottom: 1.125em; }
.blog-item .small-desc { color: #626160; }
.blog-item .small-desc p { margin-bottom: 1.25rem; }

footer { background: var(--broken-white); padding-top: 4rem; }
footer .footer-top { padding-bottom: 4rem; }
footer .logo { width: 50%; }
footer .endpage { border-top: 1px solid #000; padding: 1rem 0; line-height: 1.5; }
footer .endpage a { color: inherit; }
footer .endpage a:hover { color: var(--purple); }
footer menu { padding: 0; margin: 0; list-style: none; display: flex; flex-direction: column; flex-wrap: wrap; max-height: calc(1.6rem * 6); }
footer menu li { line-height: 1.6; }
footer menu li a { color: inherit; text-decoration: none; }
footer menu li a:hover { text-decoration: underline; }
footer .socials { display: flex; justify-content: flex-end; gap: 0.5rem; }
footer .socials a { width: 2.5rem; height: 2.5rem; font-size: 1.25rem; flex-shrink: 0; border-radius: 50%; border: 1px solid #000; display: flex; justify-content: center; align-items: center; text-decoration: none; color: #000; }
footer .socials a i.ph-facebook-logo { transform: translateX(2%); }
footer .socials a:hover { background: #000; color: var(--broken-white); }

.full-menu { position: fixed; top: 0; left: 0; width: 100%; height: 100%; overflow-x: hidden; overflow-y: auto; z-index: 5; display: none; }
.full-menu .overlay-wall { position: absolute; width: 100%; height: 100%; top: 0; left: 0; z-index: -1; transition: backdrop-filter 0.3s, background-color 0.3s; }
.full-menu .modal-content { width: 55%; height: 100%; margin-left: 45%; overflow-y: auto; background: var(--green-grey); transform: translateX(100%); transition: transform 0.6s; transition-timing-function: cubic-bezier(0,0.65,0,1); }
.full-menu .modal-content .content-wrap { height: max-content; min-height: 100%; padding: 3rem 5vw; display: grid; grid-template-rows: 1fr auto 1fr; }
.full-menu .top-and-bottom { display: flex; flex-direction: column; }
.full-menu .bottom-content { margin-top: auto; color: #fff; max-width: 30rem; }
.full-menu .menu-content { display: flex; overflow: hidden; min-height: max-content; }
.full-menu .menu-content > * { width: 100%; flex-shrink: 0; flex-grow: 0; flex-basis: 100%; transition: transform 0.3s; }
.full-menu .menu-content > *.sub-menu-open { transform: translateX(-100%); }
.full-menu .menu-content .btn-back { background: none; border: none; outline: none; color: #fff; margin-bottom: 1.5rem; transition: transform 0.3s, opacity 0.3s; }
.full-menu .menu-content .btn-back:hover { transform: translateX(-0.25rem); opacity: 0.6; }
.full-menu .menu-content .btn-back:focus-visible { text-decoration: underline; }
.full-menu menu { list-style: none; padding: 0; margin: 0; }
.full-menu menu li { font-family: 'Newsreader'; font-weight: 400; }
.full-menu menu li.hidden-in-nav { display: none; }
.full-menu menu li a { position: relative; font-size: 3rem; line-height: 1; padding: 0.75rem 1rem 0.75rem 2rem; color: #fff; text-decoration: none; display: block; transition: transform 0.3s, opacity 0.3s; }
.full-menu menu li a:focus-visible { outline: none; text-decoration: underline; }
.full-menu menu.sub-menu-items li a { font-size: 2rem; }
.full-menu menu li a:hover { transform: translateX(0.25rem); opacity: 0.6; }
.full-menu menu li ul.sub-menu { display: none; }
.full-menu.active { display: block; }
.full-menu.active .modal-content { transform: none; }
.full-menu.active .overlay-wall { backdrop-filter: blur(10px); background: rgb(0 0 0 / 30%); }

.full-menu menu.nav-items > li a::before { position: absolute; top: 0; left: 0; font-size: 1rem; font-family: 'Inter'; }
.full-menu menu.nav-items > li:nth-child(1) a::before { content: '01'; }
.full-menu menu.nav-items > li:nth-child(2) a::before { content: '02'; }
.full-menu menu.nav-items > li:nth-child(3) a::before { content: '03'; }
.full-menu menu.nav-items > li:nth-child(4) a::before { content: '04'; }
.full-menu menu.nav-items > li:nth-child(5) a::before { content: '05'; }
.full-menu menu.nav-items > li:nth-child(6) a::before { content: '06'; }
.full-menu menu.nav-items > li:nth-child(7) a::before { content: '07'; }
.full-menu menu.nav-items > li:nth-child(8) a::before { content: '08'; }
.full-menu menu.nav-items > li:nth-child(9) a::before { content: '09'; }
.full-menu menu.nav-items > li:nth-child(10) a::before { content: '10'; }
.full-menu menu.nav-items > li:nth-child(11) a::before { content: '11'; }
.full-menu menu.nav-items > li:nth-child(12) a::before { content: '12'; }
.full-menu menu.nav-items > li:nth-child(13) a::before { content: '13'; }
.full-menu menu.nav-items > li:nth-child(14) a::before { content: '14'; }
.full-menu menu.nav-items > li:nth-child(15) a::before { content: '15'; }

.full-menu menu.sub-menu-items > li a { padding-left: 1rem; }
.full-menu menu.sub-menu-items > li a::before { content: '- '; }
.full-menu menu.sub-menu-items h2 { font-size: 3.5rem; font-family: 'Newsreader'; font-weight: 400; margin-bottom: 0.25em; color: #fff; }


